import React from "react"
import Layout from "../components/layout"
import "../components/hire.css"

function hire() {
  return (
    <Layout>
      <div className="hire-container">
        <h1>Hire me?</h1>
        <div className="hire-form-container">
          <form action="https://formspree.io/mzbjbnaj" method="POST" id="form">
            <label htmlFor="Email">Email</label>
            <br />
            <input
              type="email"
              name="email"
              id="email"
              placeholder="johndoe@asdf.com"
            />
            <br />
            <label htmlFor="budget">Budget</label> <br />
            <input
              type="number"
              name="budget"
              placeholder="$100"
              id="budget"
            />{" "}
            <br />
            <label htmlFor="description">
              A short description of your project...
            </label>{" "}
            <br />
            <textarea
              name="description"
              id="description"
              cols="30"
              rows="10"
              placeholder="I want you to create an e-commerce website."
            ></textarea>{" "}
            <br />
            <input type="submit" value="send" />
          </form>
        </div>
      </div>
    </Layout>
  )
}

export default hire
